<template>
    <div>
        <navbar title="备份账号" v-if="$phonemodel == 'Android'"></navbar>
        <div class="appbox" :class="{nav:$phonemodel == 'Android'}">
            <div class="popup">
                <p class="tip">请截图或者使用文本，妥善备份您的账号密码，账户一旦丢失，账户内的资产永久不可找回。</p>
                <div class="main">
                    <p class="top">您的账号</p>
                    <p class="bottom">{{backupData.username}}</p>
                    <button class="copy"  @click="copy(backupData.password)">复制账号</button>
                </div>
                <div class="main pwd">
                    <p class="top">您的密码</p>
                    <p class="bottom">{{backupData.password}}</p>
                    <button class="copy" @click="copy(backupData.password)">复制密码</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { toast, Dialog } from 'vant';
    import {backup} from "../../api/center";
    import Clipboard from 'clipboard'
    import navbar from "@/components/navbar";
    export default {
        name: "Backup",
        components: {
            navbar
        },
        data()
        {
            return {
                backupData:{
                    username:'',
                    password:''
                }
            }
        },
        mounted()
        {
            this.getBackup()
        },
        methods: {

            getBackup()
            {
                backup().then(res => {
                    this.backupData = res

                })
            },

            // 复制邀请码
            copy(copyText)
            {

                var clipboard = new Clipboard('.copy', {
                    text: function() {
                        return copyText;
                    }
                });
                clipboard.on('success', e => {

                    toast('复制成功')
                    // 释放内存
                    clipboard.destroy()
                })
                clipboard.on('error', e => {
                    toast('复制失败, 当前浏览器不支持')
                    // 释放内存
                    clipboard.destroy()
                })

            },

        }
    }
</script>

<style scoped>
    @import "../../static/css/backup.css";
</style>



